import noData from '@/components/noData'

export default {
    components:{
        noData
    },
	 props:{
	    icon: {
	        type: Boolean,
	        default: false
	    }  
	},
    data(){
        return{
            title: '',
            user: this.$store.state.userInfo.Data,
            haveMore: true,
            page: 1,
            list: [],
            reason: [],
            pageShow: false,
			defaultImg: 'this.src="' + require('../../assets/images/special.png') + '"',
			referral:this.$route.query.Referral,
			showBtn:false,
			 Patient: this.$store.state.patient,
			 isImEntry:this.$route.query.isImEntry,
		}
    },
    created(){
        this.pull(false)
		this.pullReferral()
		this.showTitle()
    },
    mounted(){
        this.getMore()
    },
    beforeDestroy(){
        window.onscroll = null
    },
    methods: {
		 back(){
			 if(this.isImEntry){
				const data = '123'
                this.$bridge.callhandler('BackToIM', data)
			 }else if(this.referral == 1){
				 this.$router.back()
			 }else{
				 const data = '123'
				 this.$bridge.callhandler('Back', data)
			 }
		 },
		showTitle(){
			if(this.referral == 1){
				this.title = '转诊'
			}else{
				this.title = '专科协作'
			}
		},
		goChoose(i){
			if(this.referral == 1){
				this.$router.push({
					path: "/chooseDr",
					query: {
						PatientId: this.Patient.PatientId,
						PatientName: this.Patient.PatientName,
						SpecialistHosGroupId:i.SpecialistHosGroupId,
						HosGroupName:i.HosGroupName
					}
				})
			}else{
				this.$router.push(`/choosePatient`)
				this.$router.push({
					path: "/choosePatient",
					query: {
						SpecialistHosGroupId:i.SpecialistHosGroupId,
						HosGroupName:i.HosGroupName
					}
				})
			}
		},
		pullReferral(){
			if(this.referral == 1){
				this.$get('PlatFormAPI/DoctorReferral/ExistDoctorDisGroup',{
				    DrId:this.user.User.DoctorId,
				}).then(o => {
				    if(o){
				    	this.showBtn = true	
				    }
				})
			}
		},
        pull(h){
            if(this.haveMore){
                this.$post('PlatFormAPI/SpecialistHosGroup/QueryDrSpecialistHosGroupForPage',{
                    DrId: this.user.User.DoctorId,
					HosGroupName:"",
                    PageIndex: this.page,
                    PageSize: 15,
                }).then(o => {
                    this.pageShow = true
                    if (!o.ReturnData || o.ReturnData.length < 15){
                        this.haveMore = false
                    }
                    else {
                        this.haveMore = true
                    }
                    if (h){
                        o.ReturnData.forEach(v => {
                            this.list.push(v)
                        })
                    }
                    else {
                        this.list = o.ReturnData
                    }
                })
            }
        },
      
        getMore(){
            const el = this.$refs.el
            let timer = null
            window.onscroll = () => {
                clearTimeout(timer)
                timer = setTimeout(() => {
                    const top = document.documentElement.scrollTop
                                || window.pageYOffset
                                || document.body.scrollTop,
                            h = el.scrollHeight,
                            wh = window.innerHeight
                    if (h - top - wh < 100 && this.haveMore){
                        this.page += 1
                        this.pull(true)
                    }
                }, 100)
            }
        }
    }
}



